                    <div slot="header" class="clearfix">
                        <span style="font-size:20px"></span>
                    </div>
                    <el-tabs v-model="activeName" @tab-click="handleClick" class="info">
                        <el-tab-pane label="" name="first">{{ target }}</el-tab-pane>
                        <el-tab-pane label="" name="second">{{ courses }}</el-tab-pane>
                        <el-tab-pane label="" name="third">{{ exployment }}</el-tab-pane>
                    </el-tabs>
                </el-card>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    data() {
      return {
        activeName: 'first',
        exployment: "\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0",
        target: "\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0 \xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0 \r\n  \r\n\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\r\n\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa01\r\n\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa02\r\n\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa03\r\n\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa04\r\n\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa05\r\n\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa06\r\n \r\n\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0\r\n\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0",
        courses: "\xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa0  \r\n \xa0\xa0\xa0\xa0\xa0\xa0\xa0\xa06"
      };
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      }
    }
  };
</script>

<style scoped>
    .header {
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .info {
        white-space: pre-line;
    }
</style>


